header {
    height: 100vh;
    padding-top: 7rem;
    overflow:hidden;
}

.header__container{
    text-align: center;
    height:100%;
    position: relative;
}


/*cta style*/

.cta{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;

}

/*socials*/

.header__socials{
    display:flex;
    flex-direction: column;
    align-items: center;
    gap:0.8rem;
    position: absolute;
    left:0;
    bottom: 3rem;

}

.header__socials ::after{
    content: '';
    width:1px;
    height: 2rem;
    background:var(--color-primary);
}
/*image*/

.me{
    background: linear-gradient(var(--color-primary), transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 50%;
}

img{
    border-radius: 50%
}